'use client';
import { useRouter } from 'next/navigation';
import { useEffect, useRef, useState } from 'react';
import { useError } from '@/contexts/ErrorContext';

const BASE_WIDTH = 1920;
const BASE_HEIGHT = 1080;

export default function Game1() {
  const router = useRouter();
  const containerRef = useRef();
  const image2Ref = useRef();
  const [scale, setScale] = useState(1);

  const [pos, setPos] = useState({ x: 0, y: 0 });
  const [localXs, setLocalXs] = useState([]);
  const { errorCount, incrementError } = useError();

  const debug = true;

  // 畫面縮放與置中
  useEffect(() => {
    const resize = () => {
      const container = containerRef.current;
      const scaleX = window.innerWidth / BASE_WIDTH;
      const scaleY = window.innerHeight / BASE_HEIGHT;
      const newScale = Math.min(scaleX, scaleY);
      const offsetX = (window.innerWidth - BASE_WIDTH * newScale) / 2;
      const offsetY = (window.innerHeight - BASE_HEIGHT * newScale) / 2;
      container.style.transform = `scale(${newScale})`;
      container.style.left = `${offsetX}px`;
      container.style.top = `${offsetY}px`;
      setScale(newScale);
    };
    resize();
    window.addEventListener('resize', resize);
    return () => window.removeEventListener('resize', resize);
  }, []);

  // 錯誤次數達 3 自動跳轉
  useEffect(() => {
    if (errorCount >= 3) {
      router.push('/game/be');
    }
  }, [errorCount, router]);

  const handleCorrectClick = () => {
    router.push('/game/game2');
  };

  const handleWrongClick = (e) => {
    if (!image2Ref.current || errorCount >= 3) return;

    const imageRect = image2Ref.current.getBoundingClientRect();
    const clickX = e.clientX;
    const clickY = e.clientY;

    if (
      clickX >= imageRect.left &&
      clickX <= imageRect.right &&
      clickY >= imageRect.top &&
      clickY <= imageRect.bottom
    ) {
      const containerRect = containerRef.current.getBoundingClientRect();
      const x = (clickX - containerRect.left) / scale;
      const y = (clickY - containerRect.top) / scale;

      const id = Date.now();
      const targetPositions = [
        { endX: 100, endY: 100 },
        { endX: 200, endY: 100 }
      ];
      const next = targetPositions[errorCount] || { endX: 300, endY: 100 };

      setLocalXs((prev) => {
        if (prev.length >= 2) return prev;
        return [...prev, { id: Date.now(), x, y }];
      });

      incrementError();
    }
  };

  return (
    <div
      className="w-screen h-screen relative overflow-hidden"
      style={{ backgroundColor: 'black' }}
      onClick={handleWrongClick}
    >
      <div
        ref={containerRef}
        style={{
          width: `${BASE_WIDTH}px`,
          height: `${BASE_HEIGHT}px`,
          position: 'absolute',
          transformOrigin: 'top left',
          backgroundColor: '#feeac5',
          overflow: 'hidden',
        }}
      >
        {/* 背景與裝飾 */}
        <img
          src="/photo/bg2.png"
          alt="背景"
          className="absolute"
          style={{
            top: '-17%',
            left: '-45%',
            width: '100%',
            height: 'auto',
            zIndex: 0,
          }}
        />
        <img
          src="/photo/1-1.png"
          alt="裝飾1"
          className="absolute"
          style={{
            top: '3%',
            left: '14%',
            width: '540px',
            height: 'auto',
            zIndex: 1,
          }}
        />
        <img
          ref={image2Ref}
          src="/photo/1-2.png"
          alt="裝飾2"
          className="absolute"
          style={{
            top: '0%',
            right: '4%',
            width: 'auto',
            height: '100%',
            zIndex: 1,
            cursor: 'pointer',
          }}
        />

        {/* 正確點擊區域 */}
        <div
          onClick={handleCorrectClick}
          style={{
            position: 'absolute',
            top: '73%',
            left: '74.5%',
            width: '110px',
            height: '110px',
            backgroundColor: debug ? 'rgba(255, 0, 0, 0.3)' : 'transparent',
            borderRadius: '50%',
            zIndex: 5,
            cursor: 'pointer',
          }}
        />

        {/* 飛行中的叉叉 */}
        {localXs.map(({ id, x, y }, idx) => (
          <img
            key={id}
            src="/photo/X.png"
            alt="X"
            className={`absolute pointer-events-none ${idx === 0 ? 'fly-x-1' : 'fly-x-2'}`}
            style={{
              top: `${y}px`,
              left: `${x}px`,
              width: '120px',
              height: 'auto',
              zIndex: 999,
              position: 'absolute',
            }}
          />
        ))}


        {/* 叉叉動畫 keyframes */}
        <style jsx global>{`
          ${localXs
            .map(
              ({ id, startX, startY, endX, endY }) => `
              @keyframes fly-${id} {
                from {
                  left: ${startX}px;
                  top: ${startY}px;
                }
                to {
                  left: ${endX}px;
                  top: ${endY}px;
                }
              }
            `
            )
            .join('\n')}
        `}</style>
      </div>
    </div>
  );
}
